<template>
  <main id='calc' class="calc">
    <div ref="display" class="display">
      <span ref="calText">{{ display }}</span>
    </div>
    <div class="buttons">
      <div class="button-row" v-for="row in buttonRows" :key="row.buttonRows">
        <div
          @click="buttonClicks[button.type](button)"
          class="button"
          v-for="button in row"
          :key="button.row"
          :class="{ orange: button.color == 'orange' }"
          :style="button.style"
        >{{ button.text }}</div>
      </div>
    </div>
  </main>
</template>

<script>
export default {
  methods: {
    performOperations() {
      this.display = this.operations[this.currentOperator](
        +this.preVal,
        +this.display
      );
    }
  },
  watch: {
    display() {
      let fonSize = 100;
      while (this.$refs.calText.offsetWidth > this.$refs.display.offsetWidth) {
        console.log(this.$refs.display.style.fontSize);
        this.$refs.display.setAttribute("style", "font-size:" + fonSize + "px");
        fonSize--;
      }
      if (this.$refs.calText.offsetWidth + 13 < this.$refs.display.offsetWidth){
 this.$refs.display.setAttribute("style", "font-size:8vmin");
      }
    }
  },
  data: vm => ({
    display: "0",
    preVal: "",
    currentOperator: "",
    operations: {
      "÷": (a, b) => a / b,
      "×": (a, b) => a * b,
      "+": (a, b) => a + b,
      "-": (a, b) => a - b
    },

    buttonClicks: {
      number(button) {
        if (vm.display === "0" && button.text != ".") {
          vm.display = button.text;
          return;
        }
        if (vm.preVal == null) {
          vm.preVal = Number(vm.display);
          vm.display = "";
        }
        if (button.text == "." && vm.display.includes(".")) return;
        vm.display += button.text;
      },
      operator(button) {
        if (vm.currentOperator) {
          vm.performOperations();
        }
        vm.preVal = null;
        vm.currentOperator = button.text;
      },
      special(button) {
        if (button.text == "AC") {
          vm.display = "0";
          vm.preVal = "";
          vm.currentOperator = "";
        } else if (button.text == "+/-" && vm.display != "0") {
          vm.display *= -1;
        } else if (button.text == "=") {
          vm.performOperations();
          vm.currentOperator = "";
        }
      }
    },

    buttonRows: [
      [
        {
          text: "AC",
          type: "special"
        },
        {
          text: "+/-",
          type: "special"
        },
        {
          text: "%",
          type: "special"
        },
        {
          text: "÷",
          type: "operator",
          color: "orange"
        }
      ],
      [
        {
          text: "7",
          type: "number"
        },
        {
          text: "8",
          type: "number"
        },
        {
          text: "9",
          type: "number"
        },
        {
          text: "×",
          type: "operator",
          color: "orange"
        }
      ],
      [
        {
          text: "4",
          type: "number"
        },
        {
          text: "5",
          type: "number"
        },
        {
          text: "6",
          type: "number"
        },
        {
          text: "-",
          type: "operator",
          color: "orange"
        }
      ],
      [
        {
          text: "1",
          type: "number"
        },
        {
          text: "2",
          type: "number"
        },
        {
          text: "3",
          type: "number"
        },
        {
          text: "+",
          type: "operator",
          color: "orange"
        }
      ],
      [
        {
          text: "0",
          type: "number",
          style: "flex: 0 0 50%; justify-content: right; padding-left:1.4em"
        },
        {
          text: ".",
          type: "number"
        },
        {
          text: "=",
          type: "special",
          color: "orange"
        }
      ]
    ]
  })
};
</script>

<style>
* {
  box-sizing: border-box;
}
html,
body {
  margin: 0;
  height: 100%;
  overflow: hidden;
}

body {
  height: 100vh;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 5vmin;
  user-select: none;
}
main {
  background: #989898;
}
.display {
  height: 20%;
  display: flex;
  justify-content: flex-end;
  align-items: center;

  color: #fff;
  font-size: 8vmin;
  user-select: all;
}
.calc {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  width: 65vmin;
  height: 90vmin;
  font-family: sans-serif;
  border-radius: 5px;
  overflow: hidden;
}
.buttons {
  text-align: center;
  display: flex;
  flex-direction: column;
  height: -webkit-fill-available;
  height: -moz-available;
  height: fill-available;
  user-select: none;
}
.button-row {
  display: flex;
  height: 100%;
  justify-content: space-around;
}
.button {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #989898;
  width: 100%;
  padding: 0.4em;
  background: #d0d0d0;
  transition: background-color 0.2s ease-out, transform 0.5s ease-out;
  cursor: pointer;
}

.orange {
  background-color: #f6872c;
  color: white;
}
.button:active {
  background-color: #b5b4b3;
  transform: scale(0.97);
  transform: translateY(0.5vmin);
}

.orange:active {
  background-color: #db690b;
}

span {
  display: inline-block;
  padding: 0.73em;
}
</style>
